body {
  background-color: #383355;
}

.header {
  text-align: center;
  justify-content: center;
}

.header img {
  width: 10rem;
}

.champSplash, .champName, .champTitle {
  text-align: center;
  text-transform: uppercase;
  color: #d6d6d6;
  justify-content: center;
  width: 19rem;
  font-family: Brush Script MT, cursive;
  font-size: 17px;
}

.champName {
  background: linear-gradient(#1f1f1f, #d4af37);
}

.champTitle {
  background: linear-gradient(#d4af37, #1f1f1f);
}

.card {
  perspective: 600px;
  background-color: #0000;
  height: 602px;
}

.cardInner {
  transform-style: preserve-3d;
  width: 100%;
  height: 100%;
  transition: transform .8s;
  position: relative;
}

.champSplash {
  mask-image: linear-gradient(135deg, #000 25%, #ffffff7d 50%, #000 75%);
  mask-size: 500%;
}

.card:hover {
  animation: 2s infinite alternate cardFloat;
}

.card:hover .cardInner {
  cursor: pointer;
  transform: rotateY(180deg);
}

.card:hover .cardInner .front .champSplash {
  animation: 2s ease-in infinite alternate cardShine;
}

.front, .back, .cardBack {
  backface-visibility: hidden;
  border-radius: 15px;
  width: 100%;
  height: 100%;
  margin: auto;
  position: absolute;
}

.front {
  overflow: hidden;
  transform: rotateY(180deg);
}

@keyframes cardShine {
  0% {
    mask-position: 0%;
  }

  100% {
    mask-position: -120%;
  }
}

@keyframes cardFloat {
  0% {
    transform: translate(0);
  }

  50% {
    transform: translate(0, -15px);
  }

  100% {
    transform: translate(0);
  }
}

@media only screen and (width <= 768px) {
  .champCompendium {
    width: 100%;
  }
}

@media only screen and (width >= 769px) {
  .champCompendium {
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    width: 100%;
    display: grid;
  }
}

@media only screen and (width >= 1400px) {
  .champCompendium {
    grid-template-columns: repeat(6, 1fr);
    gap: 20px;
    width: 100%;
    display: grid;
  }
}
